/* The container <div> - needed to position the dropdown content */
.dropdown {
  position: relative;
  display: inline-block;
  /* Dropdown Content (Hidden by Default) */
  /* Dropdown Button */
}
.dropdown__wrapper {
  background-color: #ebeff2;
  padding: 16px;
  border: none;
  cursor: pointer;
  border-radius: 24px;
  border: 2px solid #808191;
  font-family: "Poppins", sans-serif;
  font-weight: 500;
  font-size: 16px;
  line-height: 32px;
  color: #11142D;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
}
@media screen and (max-width: 320px) {
  .dropdown__wrapper {
    font-size: 14px;
  }
}
.dropdown__content {
  display: none;
  position: absolute;
  background-color: #f1f1f1;
  border-radius: 24px;
  width: 100%;
  height: 300px;
  overflow-y: scroll;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;
}
.dropdown__content p {
  padding: 12px 32px;
  text-decoration: none;
  display: block;
  cursor: pointer;
  text-transform: capitalize;
  font-family: "Inter", sans-serif;
  font-weight: 500;
  font-size: 14px;
  line-height: 16px;
  color: #808191;
}
.dropdown__content p:hover {
  background-color: #ddd;
}
.dropdown__button {
  height: 12px;
  width: 12px;
}
.dropdown__button:hover,
.dropdown__button:focus {
  border-color: #7c7e80;
}
.dropdown__button__container {
  border-radius: 50%;
  border: 2px solid #808191;
  width: 18px;
  height: 18px;
  display: flex;
  justify-content: center;
  align-items: center;
}
/* Show the dropdown menu (use JS to add this class to the .dropdown-content container when the user clicks on the dropdown button) */
.show {
  display: block;
}
.rotateIcon {
  transform: rotate(180deg);
}
